* {
  font-family: Segoe UI, Tahoma, sans-serif, Arial, Helvetica, sans-serif;
  background: #FAFAFA;
}

body {
  padding: 40px;
}

input {
  color: grey;
  width: 100%;
  font-size: 1.2rem;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; 
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: 5px;
}

.card img {
  max-height: 170px;
}

